*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 400;
  background-color: #fff;
  color: #000;
}

/** Global classes **/

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.icon {
  min-width: 15px;
  min-height: 15px;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

.btn {
  background-color: #5b815d;
  width: 213px;
  height: 57px;
  border-radius: 29px;
  font-weight: 600;
  color: #fffefe;
  box-shadow: 0px 7px 21px 0px rgba(55, 125, 60, 0.3);
  border: none;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  line-height: 57px;
}

.btn:hover {
  transition: 0.5s;
  background-color: #97c89a;
  color: #fff;
}

a:focus,
a:active {
  outline: 2px solid #97c89a;
}

/** Atomic classes **/

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/** General classes **/

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.mobile-menu {
  display: none;
  background-color: transparent;
  border: 2px solid #000;
  border-right-color: transparent;
  border-left-color: transparent;
  width: 30px;
  height: 20px;
  position: relative;
  transition: 0.5s;
}

.mobile-menu.closer {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 5;
}

.mobile-menu:hover,
.mobile-menu:active,
.mobile-menu:focus,
.mobile-menu.closer {
  border-top-color: #97c89a;
  border-bottom-color: #97c89a;
  outline: none;
  transform: scaleY(1.3);
}

.mobile-menu:hover:before,
.mobile-menu:active:before,
.mobile-menu:focus:before,
.mobile-menu.closer:before {
  border-bottom-color: #97c89a;
}

.mobile-menu:before {
  content: '';
  display: block;
  height: 2px;
  border-bottom: 2px solid #000;
  position: absolute;
  right: 0;
  top: 50%;
  left: 0;
  margin-top: -1px;
}

.first_screen {
  padding-top: 185px;
  background: #fff url(../img/first-scr.jpg) no-repeat;
  background-size: cover;
  padding-bottom: 265px;
  min-height: 100vh;
  background-position: 50% 50%;
}

.navigation {
  display: flex;
  justify-content: space-between;
  padding: 45px;
  align-items: center;
}

.navigation__ul {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  flex-grow: 1;
}

.navigation__ul.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.navigation__ul.isOpened li {
  margin: 0;
  text-align: center;
}

.navigation__ul.isOpened li + li {
  margin-top: 30px;
}

.navigation__ul.isOpened a {
  font-size: 24px;
  color: #fff;
}

.navigation__li + .navigation__li {
  padding-left: 15px;
}

.navigation__li_tel {
  padding-left: 54px;
}

.navigation__link {
  text-decoration: none;
  font-size: 14px;
  color: #000;
  position: relative;
}

.navigation__link:after {
  content: '';
  display: block;
  border-bottom: 1px solid #97c89a;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  transition: width 0.3s;
}

.navigation__link:hover:after {
  width: 100%;
}

.navigation__link_tel {
  text-decoration: none;
  font-size: 14px;
  color: #000;
  position: relative;
}

.navigation__link_tel:after {
  content: '';
  display: block;
  border-bottom: 1px solid #97c89a;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  transition: width 0.3s;
}

.navigation__link_tel:hover:after {
  width: 100%;
}

.slogan__text {
  width: calc(58.333333333333336% - 20px);
  font-size: 6.25vw;
  line-height: 1.3;
  margin-bottom: 50px;
}

.slogan__btn_white {
  margin-left: 25px;
  border: 2px solid #97c89a;
  background-color: transparent;
  color: #000;
}

.catalog {
  padding-top: 180px;
  padding-bottom: 85px;
}

.catalog__header {
  text-align: center;
  font-size: 48px;
  font-weight: 400;
  padding-bottom: 45px;
}

.catalog__text {
  margin: 0 auto;
  margin-bottom: 60px;
  text-align: center;
  width: 570px;
  font-size: 14px;
  line-height: 21px;
  color: #555;
}

.catalog__tab {
  margin-bottom: 35px;
  display: flex;
  justify-content: center;
  align-items: baseline;
  list-style: none;
}

.catalog__tab_li {
  font-size: 16px;
  font-weight: 600;
  padding: 2px 5px;
  border-bottom: 2px solid transparent;
  transition: 0.5s;
}

.catalog__tab_li + .catalog__tab_li {
  margin-left: 55px;
}

.catalog__tab_li.active,
.catalog__tab_li:hover,
.catalog__tab_li:focus,
.catalog__tab_li:active {
  border-bottom: 2px solid #97c89a;
}

.catalog__tab_li.active a,
.catalog__tab_li:hover a,
.catalog__tab_li:focus a,
.catalog__tab_li:active a {
  color: #5b815d;
}

.catalog__tab_a {
  text-decoration: none;
  color: #000;
  transition: 0.5s;
}

.catalog__tab_a:hover,
.catalog__tab_a:focus,
.catalog__tab_a:active {
  color: #5b815d;
}

.catalog__content {
  padding-bottom: 70px;
}

.catalog__content_li {
  list-style-type: none;
  text-align: center;
}

.catalog__content_li.active {
  display: flex;
}

.catalog__content_card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  list-style-type: none;
}

.catalog__content_photo {
  width: 100%;
  height: auto;
}

.catalog__content_title {
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  color: #010101;
  padding-top: 30px;
}

.catalog__content_price {
  font-size: 16px;
  line-height: 21px;
  color: #5b815d;
  font-weight: 700;
  padding: 15px 0px;
}

.welcome {
  padding-top: 70px;
  padding-bottom: 115px;
}

.welcome__header {
  font-size: 48px;
  font-weight: 400;
  margin-top: 20px;
  margin-bottom: 55px;
  line-height: 1.1;
}

.welcome__text {
  font-size: 14px;
  margin-bottom: 25px;
  font-weight: 400;
  line-height: 20px;
}

.welcome__btn {
  border: none;
  color: #5b815d;
  border-bottom: 1px solid #97c89a;
  font-size: 16px;
  font-weight: 600;
  background-color: transparent;
}

.welcome__slider {
  width: 100%;
}

.slider-container {
  margin-right: 20px;
  margin-bottom: 40px;
  float: left;
  text-align: right;
}

.slider-container__btn {
  margin-top: 15px;
  border: none;
  background-color: transparent;
  width: 21px;
  height: 21px;
  overflow: hidden;
}

.slider-container__btn_next {
  margin-left: 10px;
}

.slider-container__btn_prev:before {
  transform: rotate(180deg);
  transform-origin: 50% 35%;
}

.slider-container__btn:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-image: url(../img/icons/ico-arrow.svg);
  background-repeat: no-repeat;
}

.features {
  padding-top: 115px;
  margin-bottom: 45px;
  text-align: center;
}

.features__head {
  font-size: 48px;
  font-weight: 400;
  margin-top: 30px;
  padding-bottom: 35px;
}

.features__about {
  margin: 0 auto;
  font-size: 16px;
  line-height: 21px;
  width: 570px;
  padding-bottom: 85px;
}

.features__list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.features__card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  padding-top: 110px;
  padding-bottom: 15px;
  list-style-type: none;
  font-weight: 600;
  position: relative;
  background-repeat: no-repeat;
  background-size: 35px 30px;
  background-position: center 30px;
}

.features__card:before {
  content: '';
  display: block;
  position: absolute;
  width: 75px;
  height: 75px;
  left: 50%;
  top: 5%;
  border-radius: 50%;
  background-color: #eaf4eb;
  transform: translateX(-50%);
  z-index: -1;
}

.features__card + .features__card {
  border-left: 1px solid #dddddd;
}

.features__card_coffee {
  background-image: url(../img/icons/noun_Coffee_848419.svg);
}

.features__card_sweets {
  background-image: url(../img/icons/noun_muffin_572526.svg);
}

.features__card_wifi {
  background-image: url(../img/icons/noun_wifi_2452937.svg);
}

.features__card_seats {
  background-image: url(../img/icons/noun_Sofa_1526269.svg);
}

.features__icon {
  border-radius: 50%;
  margin-bottom: 20px;
  background-color: #97c89a;
  height: 75px;
  width: 75px;
  padding: 20px;
  position: relative;
  left: 100px;
}

.features__text {
  text-align: center;
  font-weight: 600;
  font-size: 16px;
  margin: 0 auto;
}

.feedback {
  margin-bottom: 185px;
}

.feedback__header {
  text-align: center;
  font-size: 48px;
  font-weight: 400;
  padding-top: 100px;
  padding-bottom: 50px;
}

.feedback__rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feedback__card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding: 50px 30px 60px 30px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  list-style-type: none;
  box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.17);
}

.feedback__photo {
  border-radius: 50%;
}

.feedback__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 50px;
  padding-bottom: 10px;
  padding-top: 20px;
}

.feedback__text {
  font-size: 16px;
  line-height: 24px;
  padding-bottom: 25px;
}

.feedback__date {
  font-size: 16px;
  line-height: 24px;
  margin-top: 10px;
  color: #777777;
  font-style: italic;
}

.contacts {
  padding-bottom: 45px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.contacts-info {
  width: 33.3333%;
  flex-grow: 1;
}

.contacts-info__header {
  font-weight: 400;
  color: #000;
  font-size: 48px;
  margin-bottom: 65px;
}

.contacts-info__p {
  font-weight: 400;
  color: #000;
  font-size: 16px;
  margin-bottom: 20px;
}

.contacts-info__p:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 1rem;
  background-repeat: no-repeat;
  transform: translateY(3px);
}

.contacts-info__p_time:before {
  background-image: url(../img/icons/ico-time.svg);
}

.contacts-info__p_address:before {
  background-image: url(../img/icons/ico-address.svg);
}

.contacts-info__p_tel:before {
  background-image: url(../img/icons/ico-phone.svg);
}

.contacts-info__p_mail:before {
  background-image: url(../img/icons/ico-email.svg);
}

.contacts-info__link {
  color: #000;
  text-decoration: none;
}

.contacts-map {
  width: 66.6667%;
}

.footer {
  text-align: center;
  font-size: 14px;
  color: #555;
  margin-bottom: 40px;
}

@media screen and (min-width: 1441px) {
  .slogan__text {
    font-size: 4.6875vw;
  }
}

@media screen and (max-width: 1100px) {
  .catalog__content_card {
    width: calc(25% - 20px);
  }

  .features__list {
    justify-content: center;
  }

  .feedback__card {
    width: calc(33.333333333333336% - 20px);
  }
}

@media screen and (max-width: 960px) {
  .catalog__content_card {
    width: calc(33.333333333333336% - 20px);
  }

  .slider-container {
    float: none;
  }

  .feedback__card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 800px) {
  .slogan__text {
    font-size: 50px;
  }

  .contacts-info__header {
    text-align: center;
  }

  .contacts-map {
    width: 100%;
    margin-top: 20px;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .catalog__content_card {
    width: calc(50% - 20px);
  }

  .feedback__rows {
    justify-content: center;
  }

  .feedback__card {
    width: calc(83.33333333333334% - 20px);
  }
}

@media screen and (max-width: 650px) {
  .mobile-menu {
    display: block;
  }

  .navigation__ul {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .catalog {
    padding-bottom: 20px;
    padding-top: 60px;
  }

  .welcome {
    padding-bottom: 0px;
    padding-top: 0px;
  }

  .features {
    margin-bottom: 40px;
    padding-top: 45px;
  }

  .feedback {
    margin-bottom: 70px;
  }

  .feedback__header {
    padding-top: 50px;
  }
}

@media screen and (max-width: 586px) {
  .catalog__text {
    width: 90%;
  }
}

@media screen and (max-width: 571px) {
  .features__about {
    width: 90%;
  }
}

@media screen and (max-width: 560px) {
  .navigation__li_tel {
    position: absolute;
    top: 70px;
  }

  .catalog__content_li ul {
    justify-content: center;
  }

  .catalog__content_card {
    width: calc(83.33333333333334% - 20px);
  }

  .feedback__rows {
    justify-content: center;
  }

  .feedback__card {
    width: calc(83.33333333333334% - 20px);
  }
}

@media screen and (max-width: 550px) {
  .features__card {
    width: calc(83.33333333333334% - 20px);
  }

  .features__card + .features__card {
    border-left: none;
  }
}

@media screen and (max-width: 486px) {
  .btn {
    display: block;
    margin: 0 auto;
    margin-top: 30px;
  }
}

@media screen and (max-width: 400px) {
  .navigation__ul {
    position: absolute;
    top: 80px;
    left: 70px;
  }
}

@media screen and (max-width: 365px) {
  .catalog__tab_li + .catalog__tab_li {
    margin-left: 30px;
  }
}

@media screen and (max-width: 330px) {
  .catalog__header {
    font-size: 32px;
  }

  .welcome__header {
    font-size: 32px;
  }

  .features__head {
    font-size: 32px;
  }

  .feedback__header {
    font-size: 32px;
  }

  .contacts-info__header {
    font-size: 32px;
  }
}

@media screen and (max-width: 320px) {
  .navigation__ul {
    left: 15px;
  }
}